<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2 id="subTitle">Essential Links</h2>
    <ul>
      <li>
        <a
          href="https://www.baidu.com"
          target="_blank"
        >
         百度地址
        </a>
        <a>测试是不是一个a标签</a>
        <input type="date" value="20180809">
        <button id="button" @click="updateTitle">改变SubTitle</button>
      </li>
    </ul>

    <ul>
      <li v-for="(value, key, index) in object" :key="key">
        <br>
        {{index}}、{{key}} : {{value}}
      </li>
      <li>
        {{object.name}}
      </li>
      <li v-for="(item, index) in items" :key="item.id">
        {{index}} - {{item}}
        <br>

      </li>
    </ul>
    <!--直接添加样式-->
    <div class="active"></div>

    <!--样式与数据绑定-->
    <div :class="{active: isActive}"></div>
    <br>
    <div :class="[activeClass, errorClass]">文字</div>
    <button @click="openVueEvent">进入一个新页面</button>
    <formVue></formVue>
  </div>
</template>

<script>
import formVue from '@/components/FormVue'
export default {
  name: 'HelloWorld',
  components: { formVue },
  data () {
    return {
      msg: '欢迎来到第一个Vue工程',
      object: {
        name: '这是一个名称',
        url: 'http://www.baidu.com',
        info: '这是一个黑网站'
      },
      items: [
        {message: '测试'},
        {message: '对象'}
      ],
      isActive: true,
      activeClass: 'active',
      errorClass: 'text-danger'
    }
  },
  methods: {
    updateTitle: function () {
      document.getElementById('subTitle').innerHTML = this.msg
    },
    openVueEvent: function () {
      this.$router.push({path: '/VueEvent'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
  .active {
    width: 100px;
    height: 100px;
    background: red;
  }
  .text-danger {
    background: yellow;
    text-align: center;
  }
</style>



