import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import VueEvent from '@/components/VueEvent'
import FormVue from '@/components/FormVue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/vueEvent',
      name: 'VueEvent',
      component: VueEvent
    },
    {
      path: '/formVue',
      name: 'formVue',
      component: FormVue
    }
  ]
})



