<template>
    <div class="vueEvent">
    测试数据
      <button @click="counter += 1">自增 1</button>
      <p>点击按钮后数字会增加1：{{counter}}</p>
      <button @click="updateCounter()"></button>
      <button @click="openFormVue()">打开表单页面</button>
    </div>
</template>

<script>
export default {
  name: 'vueEvent',
  data () {
    return {
      counter: 0,
      message: 'vue event',
      type: '1',
      active: true
    }
  },
  methods: {
    updateCounter: function () {
      alert('更新')
    },
    openFormVue: function () {
      this.$router.push({path: '/formVue'})
    }
  }
}
</script>

<style scoped>

</style>



